* {
  margin:0;
}

#nav-bar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 35px;
  background-color:#F1FFE7;
  text-align: center;
  display: flex;
  align-items: center;
  z-index: 1;
}

#nav-bar > button {
  width:60vw;
  height:100%;
  text-align: center;
  
  background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

#nav-bar > button:hover{
  background-color: #BAFF29;
}

p {
  text-align: justify;
}

.content-header {
  margin: 40px 0px;
  text-align: center;
}

#wrapper-background {
  background-color:#1A1B41;
  position: absolute;
  width:100%;
}

#main-content-container {
  background-color:#C2E7DA;
  position: relative;
  min-height: 1000px;
  width: 60vw;
  margin: 50px 15% 50px;
  border-radius: 10px;
  padding: 35px 70px 30px;
}

li {
  list-style: none;
}

#personal-img {
  max-width: 300px;
  margin: 15px;
}

.publication {
  display: flex;
  margin: 10px 0px;
}

.list-icon {
  height: 20px;
  width: auto;
  padding: 5px 12px 0px 0px;
}

#footer-container {
  position: relative;
  height: 100px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  background-color:#6290C3;
}

.footer-icon {
  margin: 20px;
  height: 50px;
  width: auto;
}